import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { isWebpSupported } from 'react-image-webp/dist/utils/index.js';
import {
	animateTitles,
	refreshScrollTrigger
} from '../../animations/animations.jsx';

//* ----------------------------------------------------------------------------
export const SectionBits = ({ baseUrl }) => {
	const isHomepage = location.pathname === '/';
	useEffect(() => {

	}, []);

	useEffect(() => {
		animateTitles(
			'.service-description__title',
			'.service-description__title',
			'.service-description__title',
			'=150',
			'=150',
		);
		refreshScrollTrigger();
	}, [location.pathname, isHomepage]);

	const getPath = (fileName) => {
		return `${baseUrl}/${fileName}`;
	};

	return (
		<div className="service-description">
			<div className="service-description__column">
				<div className="material-parallax parallax">
					<div className="parallax__image">
						<picture>
							{isWebpSupported() ? (
								<img className="parallax__image-services bg"
									src={getPath('img/main/service/fotoram_bg_05.webp')}
									alt="image" />
							)
								: (
									<img className="parallax__image-services bg"
										src={getPath('img/main/service/fotoram_bg_05.png')}
										alt="image" />
								)}
						</picture>
					</div>
				</div>
				<div className="service-description__body">
					<div className='service-description__content _container'>
						<div className='service-description__title bits-title'>
							
						</div>
						<div className="service-description__description">
							<div className="service-description__row">
								<p>   &nbsp;  &nbsp;   .  &nbsp; , &nbsp;    &nbsp; .     ,      . &nbsp;    &nbsp;  -  &nbsp; &nbsp;     !</p>
								<p><span> ,</span>  &nbsp;  &nbsp;.     &nbsp;    ,  , &nbsp;     .  ,     &nbsp;  , &nbsp;    &nbsp;   &nbsp;.</p>
							</div>

							<div className="service-description__row">
								<h2> &nbsp;  &nbsp;:</h2>
								<ul>
									<li><i className="icon-hand-pointer _icon"></i>   &nbsp;, &nbsp; . (Wav+trackout);</li>
									<li><i className="icon-hand-pointer _icon"></i>  ;</li>
									<li><i className="icon-hand-pointer _icon"></i>  &nbsp;  .</li>
								</ul>
							</div>
							<div className="service-description__row">
								<h2>:</h2>
								<ul>
									<li><i className="icon-hand-pointer _icon"></i>  - &nbsp;8000 .</li>
									<li><i className="icon-hand-pointer _icon"></i>    - &nbsp;3000 .</li>
								</ul>
								<ul>
									<li><i className="icon-volume-high _icon"></i>     .</li>
									<li><i className="icon-volume-high _icon"></i> :          ,        .</li>
									<li><i className="icon-volume-high _icon"></i> :    ,          ,    .</li>
									<li><i className="icon-volume-high _icon"></i> :        ,             .</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>

		</div>
	);
};
SectionBits.propTypes = {
	baseUrl: PropTypes.string.isRequired,
};